import { Component, h } from '@stencil/core';
import { BreadcrumbItem } from '../breadcrumbs.types';

/**
 * Styling
 *
 * Using provided custom CSS properties,
 * it is possible to style the breadcrumbs.
 *
 */
@Component({
    tag: 'limel-example-breadcrumbs-styling',
    shadow: true,
    styleUrl: 'breadcrumbs-styling.scss',
})
export class BreadcrumbsExampleStyling {
    private items: BreadcrumbItem[] = [
        {
            text: 'Home',
            icon: 'smart_home',
            iconColor: 'rgb(var(--color-cyan-light))',
        },
        {
            text: 'Products',
            icon: 'menu',
        },
        {
            text: 'Phones',
            icon: 'google_mobile_copyrighted',
        },
        {
            text: 'Accessories',
        },
    ];

    public render() {
        return <limel-breadcrumbs items={this.items} />;
    }
}
